import { mapGetters, mapMutations, mapState } from "vuex";
import mqtt from "mqtt";
export default {
  data() {
    return {
      clipped: false,
      fixed: false,
      drawer: false,
      right: true,
      rightDrawer: false,
      items: [
        {
          icon: "mdi-view-dashboard",
          title: "OVERVIEW",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] !== null,
          to: "/"
        },
        {
          icon: "mdi-desktop-classic",
          title: "CONTROL PANEL",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah1",
          // this.$auth.user.userId == 1,
          to: "/ipahStatus"
        },
        {
          icon: "mdi-desktop-classic",
          title: "CONTROL PANEL",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah2",
          // this.$auth.user.userId == 3,
          to: "/ipah2Status"
        },
        {
          icon: "mdi-desktop-classic",
          title: "CONTROL PANEL",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "tkpmPagoh",
          // this.$auth.user.userId == 8,
          to: "/tkpmPagohStatus"
        },
        {
          icon: "mdi-desktop-classic",
          title: "STATUS",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "kongPo",
          // this.$auth.user.userId == 9,
          to: "/kongPoStatus"
        },
        {
          icon: "mdi-view-dashboard",
          title: "OVERVIEW",
          state: this.$auth.hasScope("admin"),
          to: "/overview-admin"
        },
        {
          icon: "mdi-desktop-classic",
          title: "STATUS",
          state: this.$auth.hasScope("admin"),
          to: "/adminStatus"
        },
        {
          icon: "mdi-calendar",
          title: "SCHEDULE",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah1",
          // this.$auth.user.userId == 1,
          to: "/scheduleIpah1"
        },
        {
          icon: "mdi-calendar",
          title: "SCHEDULE",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah2",
          // this.$auth.user.userId == 3,
          to: "/scheduleIpah2"
        },
        {
          icon: "mdi-calendar",
          title: "SCHEDULE",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "tkpmPagoh",
          // this.$auth.user.userId == 8,
          to: "/scheduleTkpmPagoh"
        },
        {
          icon: "mdi-calendar",
          title: "SCHEDULE",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "kongPo",
          // this.$auth.user.userId == 9,
          to: "/scheduleKongPo"
        },
        {
          icon: "mdi-chart-areaspline",
          title: "TRENDS",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah1",
          // this.$auth.user.userId == 1,
          to: "/trendsIpah1"
        },
        {
          icon: "mdi-chart-areaspline",
          title: "TRENDS",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "ipah2",
          // this.$auth.user.userId == 3,
          to: "/trendsIpah2"
        },
        {
          icon: "mdi-chart-areaspline",
          title: "TRENDS",
          state:
            this.$auth.hasScope("user") &&
            this.$auth.$state.user.station[0] == "tkpmPagoh",
          // this.$auth.user.userId == 8,
          to: "/trendsTkpmPagoh"
        }
      ],
      miniVariant: false,
      title: "SMART FERTIGATION DASHBOARD",
      connection: {
        host: "tron.airmode.live",
        port: 8083,
        endpoint: "/mqtt",
        clean: true, // Reserved session
        connectTimeout: 4000, // Time out
        reconnectPeriod: 4000 // Reconnection interval
      },
      subscription: {
        // topic: "geyzer/#",
        topic: ["debug/test", "nexplex/sense/tkpmIpah/controllino"],
        qos: 0
      },
      receiveNews: "",
      qosList: [
        { label: 0, value: 0 },
        { label: 1, value: 1 },
        { label: 2, value: 2 }
      ],
      client: {
        connected: false
      },
      subscribeSuccess: false
    };
  },
  methods: {
    ...mapMutations({
      ipahStatus: "ipahStatus",
      tkpmIpahStatus: "tkpmIpahStatus"
    }),
    logout: async function() {
      await this.$auth.logout();
      // this.resetState()
      this.doUnSubscribe();
      this.rightDrawer = false;
    },
    cancel: function() {
      this.rightDrawer = false;
    },
    track: function() {
      window.onclick = event => {
        const modal = document.getElementById("layer");
        if (event.target == modal) {
          this.rightDrawer = false;
        }
      };
    },
    // Create connection
    createConnection() {
      const { host, port, endpoint, ...options } = this.connection;
      const connectUrl = `wss://${host}:${port}${endpoint}`;
      try {
        this.client = mqtt.connect(connectUrl, options);
      } catch (error) {
        console.log("mqtt.connect error", error);
      }
      this.client.on("connect", () => {
        console.log("Connection succeeded!");
      });
      this.client.on("error", error => {
        console.log("Connection failed", error);
      });
      this.client.on("message", (topic, message) => {
        if (topic === "debug/test") {
          console.log("here");
          this.ipahStatus();
        }
        if (topic === "nexplex/sense/tkpmIpah/controllino") {
          message = JSON.parse(message);
          // this.ipahStatus();
          this.tkpmIpahStatus(message);
        }

        if (topic === "ipah/test") {
          message = JSON.parse(message);
          // console.log(message);
        }
      });
    },
    doSubscribe() {
      const { topic, qos } = this.subscription;
      this.client.subscribe(topic, { qos }, (error, res) => {
        if (error) {
          return;
        }
        this.subscribeSuccess = true;
      });
    },
    doUnSubscribe() {
      const { topic } = this.subscription;
      this.client.unsubscribe(topic, error => {
        if (error) {
          console.log("Unsubscribe error", error);
        }
      });
    },
    ...mapMutations({
      setActiveUser: "setActiveUser"
    })
  },
  computed: {
    ...mapGetters(["loggedInUser", "isAuthenticated"]),
    ...mapState({
      updatedDate: state => state.updated,
      updatedDate2: state => state.updated2,
      updatedDate3: state => state.updated3,
      updatedDate4: state => state.updated4
    })
  },
  mounted: function() {
    // let elHtml = document.getElementsByTagName("html")[0];
    // elHtml.style.overflowY = null;
  },
  async created() {
    // this.$store.dispatch("setForecasts");
    this.setActiveUser();
  },
  updated() {
    // this.setActiveUser();
  },
  async mounted() {
    let elHtml = document.getElementsByTagName("html")[0];
    elHtml.style.overflowY = null;
    this.createConnection();
    this.doSubscribe();
  },
  watch: {
    updatedDate: function() {
      if (this.updatedDate == true) {
        console.log("here");
        // this.client.publish("debug/test/database/ipah1", "updated");
      }
    },
    updatedDate2: function() {
      if (this.updatedDate2 == true) {
        console.log("here2");
        // this.client.publish("debug/test/database/ipah2", "updated");
      }
    },
    updatedDate3: function() {
      if (this.updatedDate3 == true) {
        console.log("here2");
        // this.client.publish("debug/test/database/tkpmPagoh", "updated");
      }
    },
    updatedDate4: function() {
      if (this.updatedDate4 == true) {
        console.log("here2");
        // this.client.publish("debug/test/database/kongPo", "updated");
      }
    }
  }
};
